export default {
	data() {
		return {

		}
	},
	props: {
		single: {
			type: Boolean,
			default: false
		},
		isMine: Boolean,
		videoItem: Object
	},
	methods: {
		handleSearch() {
			getApp().globalData.isSearch = true
			uni.navigateTo({
				url: '../../pages/search/search'
			})
		},
		handleBack() {
			if (this.isMine) {
				
				uni.switchTab({
					url: '../../pages/mine/mine'
				})
			} else {
				uni.redirectTo({
					url: '../../pages/person/person?userId=' + this.videoItem.userId
				})
			}
			
		}
	}
}
